import React, { useState } from "react";
import "../fullstack/fullstack.css";

export const Fullstack = () =>{

    const[data,setData]=useState("");


   const handleclick=()=>{
        setData(<p>HTML is a Structural layer of Website</p>)


    }

    return(
        <>
            <div className="fullstackcontainer">
                <div className="full_row">
                    <div className="full_col1">
                        <h1>FullStack Developer</h1>
                    </div>
                    <div className="full_col2">
                       
                        <ul>
                            <li><button type="button" onClick={handleclick}>HTML{data}</button></li>
                            <li><button type="button"onClick={handleclick}>CSS</button></li>
                            <li><button type="button">Bootstrap</button></li>
                            <li><button type="button">Javascript</button></li>
                            <li><button type="button">React Js</button></li>
                            <li><button type="button">MongoDb</button></li>
                            <li><button type="button">Node Js</button></li>
                            <li><button type="button">Express Js</button></li>
                        </ul>
                    </div>
                   
                </div>

            </div>
        </>
    )

};