import { CheckBox } from "@mui/icons-material";
import { useState } from "react";
import "../students/students.css";

export const Students = () => {
    const[checked,setChecked]= useState(false);
    const handlechange=()=>{
        setChecked(!checked);
    }

    const [data,FetchData] = useState([])
    const getdata = () => {
        fetch('https://deepakj29.github.io/json-data/data.json')
            .then((res) => res.json())
            .then((res) => {
                console.log(res.Students);
                FetchData(res.Students)
                console.log(res.Students);
                console.log(data);
            })
            }
    return (
        <>
            <div className="container">
                <div className="student_title">
                    <div className="list_items">
                        <button type="button" onClick={getdata}>Student List</button>
                      
                        <div  className="item_box">
                        <ul >  
                             {data.map((item, i) => { return (<li className="arr"><CheckBox value={checked} onChange={handlechange}/> Student Id:{item.id}| Name: {item.first_name} {item.last_name} |Status :{item.status} | Gender :{item.gender}| Email : {item.email}</li>) })} 
                
                             </ul>
                      

                    </div>
                </div>
            </div>

</div>
        </>
    )
};
